.ListItem__item {
  box-shadow: 20px 5px 40px 0px rgba(241, 241, 241, 0.75);
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin: 1rem 0;
  background-color: #ffffff;
}

button.cancelButton {
  background-color: #999999;
  color: white;
  margin-right: 2rem;
}

@media only screen and (min-width: 800px) {
  .ListItem {
    display: flex;
    justify-content: space-between;
  }
  
  .ListItem__item {
    display: flex;
    flex: 4;
    justify-content: space-between;
    background-color: #ffffff;
  }
  
  .ListItem__price {
    flex: 1;
    align-self: center;
    font-family: var(--header-font);
    font-weight: 300;
    text-align: right;
  }
}

